import * as React from "react";
import dayjs from 'dayjs';
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Button from "@mui/material/Button";
import Tooltip from '@mui/material/Tooltip';
import SubTitle from "./components/subtitle";
import { Typography } from "@mui/material";
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DateTimeField } from '@mui/x-date-pickers/DateTimeField';

export default function CreateGroup() {
  const [groupName, setGroupName] = React.useState("")
  const [describe, setDesceibe] = React.useState("")
  const [place, setPlace] = React.useState("")
  const [category, setCategory] = React.useState("")
  const [minnum, setMinNum] = React.useState("")
  const [maxnum, setMaxNum] = React.useState("")
  const [time, setTime] = React.useState(dayjs())
  const [finalJoinTime, setFinalJoinTime] = React.useState(dayjs())
  const [warning, setWarning] = React.useState("")

  const handleGroupNameChange = (event) => {
    setGroupName(event.target.value)
  }

  const handleDescribeChange = (event) => {
    setDesceibe(event.target.value)
  }

  const handlePlaceChange = (event) => {
    setPlace(event.target.value)
  }

  const handleCategoryChange = (event) => {
    setCategory(event.target.value);
  };

  const handleMinNumChange = (event) => {
    setMinNum(event.target.value);
  };

  const handleMaxNumChange = (event) => {
    setMaxNum(event.target.value);
  };

  const handleSubmit = () => {
    if( groupName.length===0 || describe.length===0 || place.length===0 || category.length===0 ) {
      setWarning("")
    } else if (minnum>maxnum) {
      setWarning("")
    } else if (time.diff(dayjs(), "minute") <= 30) {
      setWarning("")
    } else if (finalJoinTime.diff(time, "hour") <= 0) {
      setWarning("")
    } else {
      setWarning("")
    }
  }

  const categories = [
    "",
    "",
    "",
    "",
    "",
  ];

  return (
    <React.Fragment>
      <SubTitle name=""/>
      <Paper elevation={3} sx={{ marginRight: "20%", marginLeft: "20%"  }}>
        <Box sx={{ padding: 5 }}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={12}>
              <Tooltip title="30" arrow placement="bottom-end">
                <TextField
                  fullWidth
                  size="small"
                  label="" variant="outlined"
                  onChange={handleGroupNameChange}
                  inputProps={{ maxLength: 30 }}
                />
              </Tooltip>
            </Grid>
            
            <Grid item xs={12} sm={12}>
              <Tooltip title="300" arrow placement="bottom-end">
                <TextField
                  multiline
                  fullWidth
                  size="small"
                  rows={3}
                  label="" variant="outlined"
                  onChange={handleDescribeChange}
                  inputProps={{ maxLength: 300 }}
                />
              </Tooltip>
            </Grid>

            <Grid item xs={12} sm={12}>
              <Tooltip title="50" arrow placement="bottom-end">
                <TextField
                  fullWidth
                  size="small"
                  label="" variant="outlined"
                  onChange={handlePlaceChange}
                  inputProps={{ maxLength: 50 }}
                />
              </Tooltip>
            </Grid>

            <Grid item xs={12} sm={6}>
              <FormControl fullWidth size="small">
                <InputLabel id="demo-simple-select-label"></InputLabel>
                <Select
                  label=""
                  value={category}
                  onChange={handleCategoryChange}
                >
                  {categories.map((item) => (
                    <MenuItem value={item}>{item}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid item xs={12} sm={3}>
              <FormControl fullWidth size="small">
                <InputLabel id="demo-simple-select-label"></InputLabel>
                <Select
                  label=""
                  value={minnum}
                  onChange={handleMinNumChange}
                >
                  {[...Array(30).keys()].map((item) => (
                    <MenuItem value={item+1}>{item+1}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid item xs={12} sm={3}>
              <FormControl fullWidth size="small">
                <InputLabel id="demo-simple-select-label"></InputLabel>
                <Select
                  label=""
                  value={maxnum}
                  onChange={handleMaxNumChange}
                >
                  {[...Array(30).keys()].map((item) => (
                    <MenuItem value={item+1}>{item+1}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DateTimeField
                  label=""
                  fullWidth
                  size="small"
                  value={time}
                  onChange={(newValue) => setTime(newValue)}
                />
              </LocalizationProvider>
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DateTimeField
                  label=""
                  fullWidth
                  size="small"
                  value={finalJoinTime}
                  onChange={(newValue) => setFinalJoinTime(newValue)}
                />
              </LocalizationProvider>
            </Grid>

            <Grid item xs={12} sm={12} sx={{ textAlign: 'center'}}>
              <Typography variant="caption" display="block" sx={{ color: "red" }}>
                {warning}
              </Typography>
              <Button variant="contained" sx={{ color: "white" }} onClick={handleSubmit}>
                
              </Button>
            </Grid>
          </Grid>
        </Box>
      </Paper>
    </React.Fragment>
  );
}
