import * as React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import CssBaseline from '@mui/material/CssBaseline';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import Slide from '@mui/material/Slide';
import { Button } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import { useNavigate } from "react-router-dom";

function HideOnScroll(props) {
  const { children, window } = props;
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
  });

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

HideOnScroll.propTypes = {
  children: PropTypes.element.isRequired,
  window: PropTypes.func,
};

export default function HideAppBar(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const navigate = useNavigate();

  function handleClick(event) {
    if (anchorEl !== event.currentTarget) {
      setAnchorEl(event.currentTarget);
    }
  }

  function handleClose() {
    setAnchorEl(null);
  }

  function logout() {
    localStorage.clear()
    window.location.reload(true)
  }

  const MyMenuItem = ({ to, name }) => {
  
    const navigate = useNavigate();
  
    return (
        <MenuItem onClick={() => 
            { navigate(`/${to}`) }}>
            {name}
        </MenuItem>
    )
  }

  function CreateGroupButton() {
    const isLoggedIn = localStorage.getItem('login')
    if (isLoggedIn) {
      return (
        <Button onClick={() => { navigate('/creategroup') }}>
            
        </Button>
      )
    }
    return <div></div>;
  }

  function Drawer() {
    const isLoggedIn = localStorage.getItem('login')
    if (isLoggedIn) {
      return (
        <div>
              <Button onClick={handleClick} onMouseOver={handleClick} >
                
              </Button>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
                MenuListProps={{ onMouseLeave: handleClose }}
              >
                <MyMenuItem to='profile' name='' />
                <MyMenuItem to='mygroup' name=''/>
                <MenuItem onClick={logout}></MenuItem>
              </Menu>
            </div>
      )
    }
    return <div></div>;
  }

  return (
    <React.Fragment>
      <CssBaseline />
      <HideOnScroll {...props}>
        <AppBar color="primaryLight">
          <Toolbar sx={{ justifyContent: "space-between" }}>

            { CreateGroupButton() }
            
            <Typography variant="h5"  sx={{fontWeight: 'bold'}} onClick={() => { navigate('/') }} >
               Date
            </Typography>


            { Drawer() }
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar />
    </React.Fragment>
  );
}

